import { Message, MessageBox, Notification } from 'element-ui';
import env from '../../env.js';

/**
 * 修改图片地址
 * @param {string||Array} urllist 图片地址
 * @returns 完整的地址
 */
function imglisturl(urllist) {
  let Gurl = env.API_BASE_URL
  // console.log(typeof urllist)
  // 判断是否是数组
  if (Array.isArray(urllist)) {
    let arr = []
    for (let i = 0; i < urllist.length; i++) {
      arr[i] = Gurl + urllist[i]
    }
    return arr
  }
  // 判断是否是字符串
  if (typeof urllist === "string") {
    if (urllist.split(",")) {
      let newimglist = urllist.split(",")
      for (let i = 0; i < newimglist.length; i++) {
        newimglist[i] = Gurl + newimglist[i]
      }
      return newimglist
    }
    return Gurl + urllist

  }

}
/**
 * 修改图片编辑时地址
 * @param {Array} list 图片地址
 * @returns 
 */
function editimglist(list) {
  let arr = []
  list.forEach((item, index) => {
    // console.log(item)
    if (item.response) {
      arr = item.response.data
    } else {
      const url = new URL(item.url);
      arr.push(url.pathname)
    }

  })
  // console.log(arr)
  return arr
}
/**
 * 弹窗
 * @param {object} res 后台信息
 * @param {string} title 成功的标题
 * @param {string} titleerr 失败的标题
 * @returns 
 */
function ts(res, title, titleerr) {

  return new Promise((resolve, reject) => {
    if (res.code == 0) {
      Notification.error({
        title: titleerr ? titleerr : '失败',
        message: res.msg,
        type: "success",
      })
      reject('0')

    } else {
      Notification({
        title: title ? title : '成功',
        message: res.msg,
        type: "success",
      })
      resolve('1')
    }
  })
}

/**
 * 判断是否为空
 * @param {object} obj 判断的对象
 * @returns true //为空
 */
function pdobj(obj) {
  if (typeof obj === "undefined" || obj === null || Object.keys(obj).length === 0) {
    return true
  } else {
    return false

  }
}

/**
 * 递归树状结构查询
 * @param {树形结构每一项} tree 
 * @param {要查的值} target 
 * @returns 
 */
function findValue(item, target) {
  // console.log(item, "====")
  if (item.path === target) {
    return item;
  }
  if (item.children && item.children.length > 0) {
    for (let i = 0; i < item.children.length; i++) {
      let result = findValue(item.children[i], target);
      if (result) {
        return result;
      }
    }
  }
  return null;
}

/**
 * 节流
 * @param {Function} func 
 * @param {Number} delay 
 * @returns 
 */
function throttle(func, delay = 1000) {
  let timerId;
  let lastExecutedTime = 0;

  return function () {
    const context = this;
    const args = arguments;
    const currentTime = Date.now();

    if (currentTime - lastExecutedTime < delay) {
      clearTimeout(timerId);

      timerId = setTimeout(function () {
        lastExecutedTime = currentTime;
        func.apply(context, args);
      }, delay);
    } else {
      lastExecutedTime = currentTime;
      func.apply(context, args);
    }
  };
}



export default { ts, imglisturl, pdobj, editimglist, throttle }