import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './router.js'
import { asyncRoutes } from './router.js'
import _self from '../utils'
import Vuex from 'vuex'
import stores from '../store'


Vue.use(Vuex);
const store = new Vuex.Store({
    ...stores
});


const router = new VueRouter({
    mode: 'hash',
    base: '/office/',
    routes
});
Vue.use(VueRouter)

// 重置路由(退出时调用)
export function resetRouter() {
    const newRouter = new VueRouter({
        routes
    })
    // console.log(newRouter)
    router.matcher = newRouter.matcher
}
/**
 * 
 * @param {object} item 查找的路由每一项
 * @param {string} target 要查找得值
 * @param {string} value 重新赋予的页面路路径
 */
function findrouterValue(item, target, value) {
    // console.log(item, "====")
    if (item.component && item.component === target) {
        item.component = value
    }
    if (item.children && item.children.length > 0) {
        for (let i = 0; i < item.children.length; i++) {
            findrouterValue(item.children[i], target, value);
        }
    }
}
// console.log(stores.state.routes,'0000000000000000')

//动态添加路由
export function generateRoutes() {
    const _asyncRoutes = JSON.parse(JSON.stringify(store.state.routes))
    // console.log(_asyncRoutes,'=-=-=-=-=-')
    if (!_asyncRoutes || (_asyncRoutes && !_asyncRoutes.length > 0)) {

        return
    }
    const routeslist = _asyncRoutes.map(({ path, name, component, redirect, meta, children }) => {
        const route = { path, name, component, redirect, meta, children }

        for (const [key, value] of Object.entries(asyncRoutes)) {
            findrouterValue(route, key, value)
        }
        // console.log(route,'=-=-=-=-=-')
        return route
    })
    // console.log(routeslist, '=-=-=-=-=-')

    //导航栏
    // store.commit('setmenulist', _asyncRoutes)
    // 路由添加
    router.addRoutes(routeslist)

}

// 检查是否存在于免登陆白名单(未登录，无token)
function inWhiteList(toPath) {
    const whiteList = []
    const path = whiteList.find((value) => {
        // 使用正则匹配
        const reg = new RegExp('^' + value)
        return reg.test(toPath)
    })
    return !!path
}

