import Vue from 'vue';


import App from './App.vue'
import './assets/js/rem.js'
import './assets/css/style.css'

import env from "../env.js";//地址
Vue.prototype.$env = env;


import VueParticles from 'vue-particles'
Vue.use(VueParticles)//粒子背景


import VueWechatTitle from 'vue-wechat-title'
Vue.use(VueWechatTitle)//title

import axios from './http';
Vue.prototype.$http = axios;

import axios1 from 'axios';
Vue.prototype.$axios = axios1;


import { EventBus } from './utils/EventBus';
Vue.prototype.$EventBus = EventBus;



import _self from './utils'
Vue.prototype.$self = _self;

import Vuex from 'vuex'
import stores from './store'
Vue.use(Vuex);

// 导入按钮权限自定义指令
// import directive from './permission/index.js'
// Vue.use(directive)

const store = new Vuex.Store({
	...stores
});


import ElementUI from 'element-ui'
import './assets/css/index.css'
Vue.use(ElementUI)
// 表格
import ElTable from '@/utils/elTable.js';
Vue.use(ElTable);

import router from './router/index.js'


Vue.config.productionTip = false




new Vue({
	store,
	router,
	render: h => h(App),
}).$mount('#app')

